.appContainer {
  margin: 0;
  padding: 0;
  text-decoration: none;
  outline: none;
  border: none;
  text-transform: capitalize;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  .greeting{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    width: 50%;
    height: 60rem;
    margin-top: 2rem;
  }
  .content {
    position: relative;
    height: 100%;
    width: 50%
  }
}
@media screen and (max-width: 768px){
  .appContainer{
    flex-direction: column;
    .greeting{
      position: absolute;
      left: -500px
    }
    .content{
      width: 100%;
    }
  }
}